.card {
  background: linear-gradient(to bottom, rgba(60, 60, 68, 1) 0%, rgba(234, 190, 63, 0.938813025210084) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media (min-width: 768px) {
    width: rem(370);
    margin: rem(20) 0 rem(20) 0;
    border-radius: rem(6);
  }
  @media (min-width: 1024px) {
    width: rem(585);
  }
  @media (min-width: 1100px) {
    width: rem(600);
  }
  @media (min-width: 1100px) {
    width: rem(650);
  }

  &__button {
    background-color: $reset-bkg;
    border: solid 1px $border-logo;
    color: $white;
    margin: 38px 0 20px 0;
    font-size: rem(12);
    line-height: rem(20);
    padding: 4px 12px 5px 10px;
    border-radius: 15px;
    transform: translateX(-115%);
    text-transform: uppercase;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }
  }

  &__article {
    background-color: $white-two;
    padding: 21px 0 12px 0;
    box-shadow: 0 1px 10px 0 $black, 0 1px 3px 0 rgba(0, 0, 0, 0.35);
    border-radius: 2px;
    margin-bottom: 40px;
    height: rem(329);
    width: rem(240);

    &--data {
      position: relative;
      border-left: solid 5px $dirty-blue;
      .name {
        //color: $dark-green-blue;
        font-family: $cardFont;
        font-size: rem(18);
        font-weight: 700;
        text-transform: capitalize;
        margin: 0 14px 4px 15px;
        line-height: rem(20);
      }
      .text {
        color: $gunmetal;
        font-family: $cardFont;
        font-size: rem(16);
        margin: 0 14px 4px 15px;
        line-height: rem(20);
        font-weight: 400;
      }
    }
    &--photo {
      background: url('../../images/giphy.harry.gif');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      height: 200px;
      margin: 10px 0;
    }
    &--links {
      display: flex;
      margin: 10px 50px 0 20px;

      .icon {
        color: $dark-green-blue;
        border-radius: 16px;
        margin: 0 10px 0 0;
        padding-top: rem(5);
        text-align: center;
        cursor: pointer;
        width: rem(32);
        height: rem(32);
      }
    }
  }
}
